<template>
    <ion-content>

    <router-link to="/controlPanel"><ion-button expand="full" color="tertiary"><ion-icon name="hammer"></ion-icon>{{$t('backoffice.list.buttons.goToControlPanel')}}</ion-button></router-link>
    <router-link to="/aboutDataSettings"><ion-button expand="full" color="tertiary"><ion-icon name="arrow-round-back"></ion-icon>{{$t('backoffice.form.buttons.backToAboutList')}}</ion-button></router-link>

    <br/>
    <ion-label><h1>{{titleT}}</h1></ion-label>

      <!-- <ion-card> -->
        <ion-item>
          <ion-label position="floating"><span style="color: red">*</span>{{$t('backoffice.form.fields.title')}}</ion-label>
          <ion-input type="text" name="title"
          @input="title = $event.target.value" 
          v-bind:value="title">
          </ion-input>
        </ion-item>
        <ion-item>
          <ion-label position="floating"><span style="color: red">*</span>{{$t('backoffice.form.fields.subtitle')}}</ion-label>
          <ion-input type="text" name="subtitle"
          @input="subtitle = $event.target.value" 
          v-bind:value="subtitle">
          </ion-input>
        </ion-item>
        <ion-item>
          <ion-label position="floating">{{$t('backoffice.form.fields.description')}}</ion-label>
          <ion-textarea name="description" 
          @input="description = $event.target.value" 
          v-bind:value="description">
          </ion-textarea>
        </ion-item>
        <ion-item>
          <ion-card v-if="checkImage()" >
              <ion-img :src="file"></ion-img>
          </ion-card>
          <ion-label position="floating"><span style="color: red">*</span>{{$t('backoffice.form.fields.image')}}</ion-label>
          <ion-input type="file" accept=".jpg,.png,.gif" name="file"
          @change="handleImage">
          </ion-input>
        </ion-item>
      <!-- </ion-card>  -->

      <br/>
      <ion-button expand="full" color="primary" @click="saveAbout()">{{ titleT }}</ion-button>

    </ion-content>
</template>

<script>

import { Api } from '../api/api.js';

export default {

  name: 'aboutForm',
   
  data () {
    return {
      modelName: 'About',
      /****** Form Data ******/
      id: null,
      title: '',
      subtitle: '',
      description: '',
      file: null,
      fileName: '',
      /***** Is editing  *****/
    }
  },
  created: function(){
    
    this.id = this.$route.params.aboutId;
    if (this.id){
      this.title = 'Edit about';
      Api.fetchById(this.modelName, this.id)
            .then(response => {
               this.title = response.data.Title;
               this.subtitle = response.data.Title;
               this.description = response.data.Description;
               this.file = response.data.ImageUrl;
               return response;
            })
            .catch(e => {
              console.log(e);
            })   
    }

    console.log(this.$route.params);
  },
  computed: {
        titleT() {
            return this.id ? this.$t('backoffice.form.titles.settingEditTitle') :  this.$t('backoffice.form.titles.settingNewTitle');
        }
  },
  methods: {
    isValidForm(){
        let errors = [];
        if (this.title == "")
        {
            errors.push(this.$t('backoffice.form.validate.title'));
        }
        if (this.subtitle == "")
        {
            errors.push(this.$t('backoffice.form.validate.subtitle'));
        }
        if (this.description == "")
        {
            errors.push(this.$t('backoffice.form.validate.description'));
        }
        if (this.file == null)
        {
            errors.push(this.$t('backoffice.form.validate.image'));
        }

        if (errors.length > 0)
        {
            let message = "";
            for (let i = 0; i < errors.length; i++) {
                 message += (i + 1) + "- " + errors[i] + "<br/>";
            }
            this.ShowMessage(this.$t('backoffice.form.validate.validate'),
                               message, this.$t('backoffice.form.validate.validateSetting'));
            return false;
        }
        else
        {
            return true;
        }
    },
    ShowMessage(type, message, topic='') {
        return this.$ionic.alertController
          .create({
            cssClass: 'my-custom-class',
            header: type,
            subHeader: topic,
            message: message,
            buttons: [this.$t('backoffice.form.messages.buttons.ok')],
          })
        .then(a => a.present())
    },
    /****** Load image use base64 encode esto debera ir en un componente******/
    checkImage: function(){
      return this.file != null;
    },
    handleImage: function(event)
    {
        const selectedImage = event.target.files[0];
        this.fileName = selectedImage.name;
        this.createBase64Img(selectedImage);
    },
    createBase64Img: function(fileObject){
        const reader = new FileReader();

        reader.onload = (e) => {
            this.file = e.target.result;
            console.log(this.file);
        };
        reader.readAsDataURL(fileObject);
    },
    /*******                              Fin                              *******/
    //Create or edit a new category
    saveAbout: function(){

        if (this.isValidForm()){

            let item = {
              "Title": this.title,
              "Subtitle": this.subtitle,
              "Description": this.description,
              "ImageUrl": "",
            }
            if (this.file != null)
            {
              item["ImageUrl"] = this.file;
              item["ImageName"] = this.fileName;
            }
            //If I am editing
            if (this.id){
              item['_id'] = this.id;
              Api.putIn(this.modelName, item)
                  .then(response => {
                        // alert("Success edited");
                        this.ShowMessage(this.$t('backoffice.list.messages.infoDeleteSuccess'),
                             this.$t('backoffice.list.messages.messageCreateSuccessSetting'), 
                                this.$t('backoffice.list.messages.titleEditSuccess'));
                        this.title = '';
                        this.subtitle = '';
                        this.description = '';
                        this.isEditing = false;
                        this.id = null;
                        this.file = null;
                        this.$router.push({
                          name: 'About', 
                        });
                        return response;
                  })
                  .catch(e => {
                        console.log(e);
                        this.ShowMessage(this.$t('backoffice.list.messages.errorTitle'),
                               this.$t('backoffice.list.messages.errorMessage'),
                                   this.$t('backoffice.list.messages.titleEditSetting'));
                  })
            }
            else{
              //Else, I am created a new category
              Api.postIn(this.modelName, item)
                  .then(response => {
                      this.ShowMessage(this.$t('backoffice.list.messages.infoDeleteSuccess'),
                             this.$t('backoffice.list.messages.messageCreateSuccessSetting'), 
                                this.$t('backoffice.list.messages.titleCreateSetting'));
                      this.title = '';
                      this.subtitle = '';
                      this.description = '';
                      this.file = null;
                      this.$router.push({
                        name: 'About', 
                      });
                      return response;
                  })
                  .catch(e => {
                      console.log(e);
                      this.ShowMessage(this.$t('backoffice.list.messages.errorTitle'),
                          this.$t('backoffice.list.messages.errorMessage'),
                              this.$t('backoffice.list.messages.titleCreateSetting'));
                  })
            }

        }
    },
  },

}
    
</script>